import React, { useState } from "react";
import { FaBars } from "react-icons/fa";
import { ImCross } from "react-icons/im";
import { NavLink } from "react-router-dom";
import styles from "./Header.module.css";
import MenuLink from "./MenuLink";
import DownloadCV from "../DownloadCV";

const Header = () => {
  const [showHamburger, setShowHamburger] = useState(true);
  const [active, setActive] = useState("home");

  const menu = [
    {
      title: "home",
      to: "#heroSection",
    },
    {
      title: "about",
      to: "#aboutSection",
    },
    {
      title: "portfolio",
      to: "#portfolioSection",
    },
    {
      title: "skills",
      to: "#skillsSection",
    },
    {
      title: "conatct",
      to: "#contactSection",
    },
  ];

  return (
    <header>
      <nav className={`${styles.navbar}`}>
        <div className={styles.logoDiv}>
          <NavLink to="/">
            <img src='./img/logo.webp' loading="lazy" className={styles.logo} alt="Logo" />
          </NavLink>
        </div>

        <ul
          className={`${styles.menu} ${!showHamburger ? styles.activeMenu : ""
            }`}
        >
          {menu.map((item, index) => (

            <MenuLink
              key={index}
              title={item.title}
              to={item.to}
              setShowHamburger={setShowHamburger}
              setActive={setActive}
              active={active}
            />
          ))}
        </ul>

        <div className={styles.hambugerDiv}>
          <div className={styles.downloadCV}>
            <DownloadCV />
          </div>
          <div className={styles.hamburger}>
            {showHamburger ? (
              <button onClick={() => setShowHamburger(!showHamburger)}>
                <FaBars fontSize={"3rem"} />
              </button>
            ) : (
              <button onClick={() => setShowHamburger(!showHamburger)}>
                <ImCross fontSize={"2rem"} />
              </button>
            )}
          </div>
        </div>
      </nav>
    </header>
  );
};

export default Header;
