import React from "react";
// import { Link } from "react-scroll";
import styles from "./Header.module.css";

const MenuLink = ({ to, setShowHamburger, title, active, setActive }) => {
  return (
    <li>
      <a
        href={to}
        onClick={() => {
          setShowHamburger(true);
          setActive(title)
        }}
        className={`${active === title ? styles.activeNavLink : ""} ${styles.navLink
          }`}
      >
        {title}
      </a>
    </li>
  );
};

export default MenuLink;
